import common from '../../util/common'
import Category from '../../util/category'

export default {
    name: 'BookDetail',
    data () {
        return {
            novels: null
        }
    },
    onLoad (option) {
        this.novels = JSON.parse(option.novels)
    },
    methods: {
        convertSex (sex) {
            return common.getSex(sex)
        },
        convertCategory (sex, category) {
            return Category[sex][category]
        },
    }
}
